import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {IGoodPropertyTemplate, IGoodsCategory, IShowEditGoodsCategory} from '../../../interfaces/interfaces';
import Swal from 'sweetalert2';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {GoodsCategoryService} from '../../../services/goods-category.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Constants} from '../../../app.constants';

@Component({
    selector: 'app-edit-category',
    templateUrl: './edit-category.component.html',
    styleUrls: ['./edit-category.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class EditCategoryComponent implements OnInit {

    errorMessage = '';
    successMessage = '';
    data: IGoodsCategory[] = [];
    categoryData: IGoodsCategory = null;
    properties?: IGoodPropertyTemplate[] = [];

    public form: FormGroup;
    public currentId;

    loading = false;
    pageLoading = false;

    constructor(private service: GoodsCategoryService, private formBuilder: FormBuilder,
                private router: Router, private route: ActivatedRoute) {
        this.currentId = this.route.snapshot.params.id;
    }

    ngOnInit() {
        this.buildForm();
        this.loadData()
    }

    buildForm() {
        this.form = this.formBuilder.group({
            name: ['', Validators.required],
            description: ['', Validators.required],
            meta_keyword: [''],
            meta_description: [''],
            parent_id: ['']
        });
    }

    loadData() {
        this.pageLoading = true;
        this.service.getGoodsCategory(this.currentId).subscribe((data: IShowEditGoodsCategory) => {
                this.pageLoading = false;
                if (!data) {
                    return;
                }

                console.log(data);
                this.categoryData = data.category;
                this.data = data.others;
                this.form.patchValue({
                    name: this.categoryData.name,
                    description: this.categoryData.description,
                    meta_description: this.categoryData.meta_description,
                    meta_keyword: this.categoryData.meta_keyword,
                    parent_id: this.categoryData.parent_id,
                });

                this.properties = [];
                if (this.categoryData.properties_template) {
                    this.categoryData.properties_template.forEach(property => {
                        this.properties.push({name: property.name, type: property.values.join(',')});
                    });
                }
            }, err => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.pageLoading = false;
            }
        );
    }

    onSubmit({value, valid}: { value: IGoodsCategory, valid: boolean }) {
        this.loading = true;
        this.errorMessage = '';
        this.successMessage = '';
        value.properties = JSON.stringify({data: this.properties});
        this.service.updateCategory(this.currentId, value).subscribe((resp: any) => {
            if (resp) {
                Swal.fire('Updated!', 'Goods category updated successfully!', 'success');
                this.router.navigate(['/app/goods-categories']);
            }
            this.loading = false;
        }, err => {
            Constants.handleError(err, this.router, true);
            this.loading = false;
        })
    }

    addNewProperty() {
        this.properties.push({name: '', type: ''});
    }

    removeProperty(property?: IGoodPropertyTemplate) {
        this.properties.splice(this.properties.indexOf(property), 1);
    }

}
