import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {CreateCategoryComponent} from './create-category/create-category.component';
import {CategoriesListComponent} from './categories-list/categories-list.component';
import {EditCategoryComponent} from './edit-category/edit-category.component';
import {RouterModule, Routes} from '@angular/router';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {LoaderModule} from '../../shared/loader/loader.module';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';

export const routes: Routes = [
    {path: 'create', component: CreateCategoryComponent},
    {path: 'edit/:id', component: EditCategoryComponent},
    {path: '', redirectTo: '/app/goods-categories/list', pathMatch: 'full'},
    {path: 'list', component: CategoriesListComponent},
];

@NgModule({
    declarations: [CreateCategoryComponent, CategoriesListComponent, EditCategoryComponent],
    imports: [
        CommonModule,
        PerfectScrollbarModule,
        RouterModule.forChild(routes),
        FormsModule, CustomErrorModule, LoaderModule,
        ReactiveFormsModule
    ]
})
export class CategoryModule {
}
