import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {IGoodPropertyTemplate, IGoodsCategory} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {GoodsCategoryService} from '../../../services/goods-category.service';
import {Router} from '@angular/router';
import {GoodsService} from '../../../services/goods.service';

@Component({
    selector: 'app-create-category',
    templateUrl: './create-category.component.html',
    styleUrls: ['./create-category.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CreateCategoryComponent implements OnInit {

    public errorMessage = '';
    public successMessage = '';
    public data: IGoodsCategory[] = [];
    loading: boolean;
    form: FormGroup;
    categories?: IGoodsCategory[] = [];
    properties?: IGoodPropertyTemplate[] = [];

    constructor(private service: GoodsCategoryService,
                private formBuilder: FormBuilder,
                private router: Router, private goodsService: GoodsService) {
    }

    ngOnInit() {
        this.buildForm();
        this.loadCategories();
    }

    private loadCategories() {
        this.goodsService.getGoodsCategory().subscribe((categories: IGoodsCategory[]) => {
            this.categories = categories;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
        });
    }


    buildForm() {
        this.form = this.formBuilder.group({
            name: ['', Validators.required],
            description: ['', Validators.required],
            meta_keyword: [''],
            meta_description: [''],
            parent_id: [''],
            details: this.formBuilder.array([])
        });
    }

    onSubmit({value, valid}: { value: IGoodsCategory, valid: boolean }) {
        this.loading = true;
        this.errorMessage = '';
        this.successMessage = '';
        value.properties = JSON.stringify({data: this.properties});
        //  console.log(value);
        this.service.addNew(value).subscribe((resp: any) => {
                this.loading = false;
                if (resp) {
                    Swal.fire('Successful!', 'New goods category created successfully!', 'success');
                    this.properties = [];
                    this.form.reset();
                }
            }, err => {
                Constants.handleError(err, this.router, true);
                this.loading = false;
            }
        )
    }

    addNewProperty() {
        this.properties.push({name: '', type: ''});
    }

    removeProperty(property?: IGoodPropertyTemplate) {
        this.properties.splice(this.properties.indexOf(property), 1);
    }

}
