import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {IGoodsCategory} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup} from '@angular/forms';
import {GoodsCategoryService} from '../../../services/goods-category.service';
import {Router} from '@angular/router';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';

declare var $: any;

@Component({
    selector: 'app-categories-list',
    templateUrl: './categories-list.component.html',
    styleUrls: ['./categories-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CategoriesListComponent implements OnInit {

    searchForm: FormGroup;
    public dataSelection = [];
    public data: IGoodsCategory[] = [];
    public errorMessage = '';
    public table;
    public showDeleteModal = false;
    public placeHolderMessage = 'Loading, please wait...';
    searching = false;

    url: string;
    current_page: number;
    from: number;
    last_page: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    to: number;
    total: number;
    totalItem: number;
    categories?: IGoodsCategory[] = [];

    constructor(private service: GoodsCategoryService,
                private router: Router,
                private formBuilder: FormBuilder) {
    }

    ngOnInit() {
        this.searchForm = this.formBuilder.group({
            searchField: [''],
        });
        this.submitSearch();
    }

    submitSearch() {
        const searchText = this.searchForm.get('searchField').value;
        // const fromDate = this.searchForm.get('fromDate').value;
        // const toDate = this.searchForm.get('toDate').value;
        // const category = this.searchForm.get('category').value;

        let param = '';
        if (searchText) {
            param += 'search=' + searchText;
        }

        // if (fromDate) {
        //     if (param) {
        //         param += '&';
        //     }
        //     param += 'from_date=' + this.getTextFromDate(fromDate);
        // }
        //
        // if (toDate) {
        //     if (param) {
        //         param += '&';
        //     }
        //     param += 'to_date=' + this.getTextFromDate(fromDate);
        // }

        // if (category) {
        //     if (param) {
        //         param += '&';
        //     }
        //     param += 'category=' + category;
        // }

        const perPage = $('#per-page-select').val();

        if (parseInt(perPage, 10)) {
            if (param) {
                param += '&';
            }
            param += 'per_page=' + perPage;
        }

        if (param) {
            param = '?' + param;
        }

        this.searchGoodsCategory('', param);
    }

    searchGoodsCategory(url, params) {
        this.searching = true;
        this.data = [];
        this.last_page = -1;
        this.service.getAllGoodsCategory(url, params).subscribe((data: any) => {

                this.data = data.data;
                this.current_page = data.current_page;
                this.from = data.from;
                this.last_page = data.last_page;
                this.next_page_url = data.next_page_url;
                this.path = data.path;
                this.prev_page_url = data.prev_page_url;
                this.per_page = data.per_page;
                this.to = data.to;
                this.total = data.total;
                this.totalItem = this.data.length;

                this.searching = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.searching = false;
            }
        );
    }

    handleDelete() {
        const text = 'Do you want to delete ' + (this.dataSelection.length === 1 ? 'the selected category' : 'the categories selected');
        Swal.fire({
            title: 'Are you sure?',
            text: text,
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.doDelete();
            }
        });
    }

    public showDeleteSingleModal(id) {
        // deselect all boxes
        this.toggleAll(false);
        this.dataSelection = [id];
        this.handleDelete();
    }

    public doDelete() {
        this.placeHolderMessage = 'Request is being processed, please wait...';
        const parems = [];
        // if(this.currentSingleSelection )
        if (this.dataSelection.length === 1) {
            this.service.deleteCategory(this.dataSelection[0]).subscribe((data: Response) => {
                    if (data) {
                        Constants.succeeded('The category was deleted successfully!').then(response => {
                            this.submitSearch();
                        });
                    }
                }, (err) => Constants.handleError(err, this.router, true)
            );
        } else if(this.dataSelection.length) {
            for (let x = 0; x < this.dataSelection.length; x++) {
                parems.push(this.dataSelection[x].id);
            }
            this.service.deleteCategories(parems).subscribe((data: Response) => {
                if (data) {
                    Constants.succeeded('The selected categories were deleted successfully!').then(response => {
                        this.submitSearch();
                    });
                }
            }, (err) => {
                Constants.handleError(err, this.router, true)
            });
        }
    }

    toggleSelect(target, item: IGoodsCategory) {
        if (target.checked) {
            if (this.dataSelection.indexOf(item.id) < 0) {
                this.dataSelection.push(item.id);
            }
        } else {
            this.dataSelection.splice(this.dataSelection.indexOf(item.id), 1);
        }
    }

    toggleAll(value) {
        const d = $('.select-box').prop('checked', !value);
        if (value) {
            this.dataSelection = [];
        }
        d.click();
    }

}
